.tabs{
    overflow: hidden;
    display: flex;     
    background-color:var(--bg-secondary);
    border-radius:25px;
    min-height:10%;
}

.tab-link{
    background-color: inherit;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 1em 3em;
    transition: 0.3s;
    flex:1;
    text-align:center;  
}

.tab-link:hover{
    background-color: var(--grey-200);
}

.tab-link.active{
    background-color: var(--primary-color);
    color: var(--text-primary);
}

.tab-content {
    display:none;
    padding: 1em 2em;
    border-top: none;
    transition: 0.3s ease-in-out;
}

.tab-content.active{
    display:block;
}   