.chat-list{
    display:flex;
    align-items: center;
    right:0;
    list-style: none;
    justify-items: center;
    
    margin-top: 3.5vh;
    margin-bottom:0;
    padding:1em;
    padding-top:2em;
    background: var(--background-burn-effect);
    backdrop-filter: var(--blur-effect-heavy);
    box-shadow: var(--dropshadow-medium);
    flex-direction: column;
    
    height:100%;
    width:100%;

    direction:ltr;

}


.chat-item{
    display:flex;
    flex-direction:row;
    box-sizing: border-box;
    overflow:hidden;
    align-items: center ;
    justify-content: center;
    padding:0.3em;
    cursor:pointer;
    background: var(--bg-glass);
    box-shadow: var(--dropshadow-medium);
    border-radius: 25px;
    width:100%;
    margin-bottom:0.5em;
    transition: 0.2s ease;
}

.chat-item:hover{
    background: var(--bg-glass-hover);
    box-shadow: var(--dropshadow-heavy);
}

#chat-i{
    justify-content: flex-start;
}

.chat-title{
    font-size:1.5em;
    margin:0;
}

.page-container{
    width:100%;
}



#chat-list-container{
    display:flex;
    height:87vh;
    top: 13vh; 
    right:0;
    left:auto;
    width: 24vw;
    overflow-y: scroll;
    justify-content: center;
    border-radius:25px;
    box-sizing:border-box;
    border-left: 2px solid var(--grey-900);
    z-index:0;

    direction:rtl;
}

#chat-avatar{
    height:5rem;
    width:5rem;
    padding:0.5em;
    border-radius: 50%;
    object-fit: cover;
}